// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import FastClick from 'fastclick'
import VueRouter from 'vue-router'
import routes from './router'
import WechatPlugin from 'vux/src/plugins/wechat/index.js'

import AjaxPlugin from 'vux/src/plugins/ajax/index.js'

import store from './store/'
import Render from './Render'
import httpinterceptor from './config/httpinterceptor'
import XButton from 'vux/src/components/x-button/index.vue'
import Toast from 'vux/src/components/toast/index.vue'

import './style/common.less'
import './style/reset.css'
import './assets/lib/zepto.js'
import './assets/lib/rem.js'
import './assets/font/iconfont.js'

// import Mint from 'mint-ui';
// Vue.use(Mint);
// console.log(Mint)

import { Cell, Checklist } from 'mint-ui';
Vue.component(Cell.name, Cell);
Vue.component(Checklist.name, Checklist);
// console.log(Cell)


Vue.use(AjaxPlugin)
Vue.use(VueRouter)
Vue.use(WechatPlugin)


const router = new VueRouter({
  routes
})


FastClick.attach(document.body);

Vue.config.productionTip = false;

/* eslint-disable no-new */
var app = new Vue({

  created(){

    httpinterceptor.init(this);

  },
  components: {
    XButton,Toast
  },
  router,
  store,
  render: h => h(Render)
}).$mount('#app-box')






